@font-face {
  font-family: "Oswald";
  src: url(/fonts/Oswald-VariableFont_wght.ttf);
}
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  max-width: 1440px;
  font-family: "Oswald", Arial, sans-serif;
  box-sizing: border-box;
  color: #111111;
  background-color: #ffffff;
}
body a {
  text-decoration: none;
}
body ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.button {
  padding: 10px 30px 10px 25px;
  min-width: 245px;
  min-height: 40px;
  color: #ffffff;
  background-color: #68b738;
}

.page-header {
  position: relative;
  min-height: 77px;
  max-height: 264px;
}
.page-header__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 24px 30px;
  }
}
@media (min-width: 1440px) {
  .page-header::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 720px;
    height: 696px;
    background: url(/img/index-background-desktop.jpg.jpg) 50% no-repeat;
  }
  .page-header__wrapper {
    padding: 50px 110px 0;
  }
}

.main-navigation {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin-left: -20px;
  margin-right: -20px;
}
@media (min-width: 768px) {
  .main-navigation {
    flex-direction: row;
  }
  .main-navigation__toggle {
    display: none;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.site-list__item {
  width: 100%;
  min-height: 66px;
  padding: 23px 0;
  text-align: center;
  border-bottom: 1px solid;
  border-bottom-color: #e6e6e6;
}
.site-list__item:first-child {
  margin-left: 0;
}
.site-list__link {
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.site-list__link:hover {
  opacity: 60%;
}
.site-list__link:active {
  opacity: 30%;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
  }
  .site-list__item {
    width: auto;
    min-height: 42px;
    margin-left: 33px;
    padding: 12px 0;
    border-bottom: none;
  }
  .site-list__item--active {
    border-bottom: 2px solid;
    border-bottom-color: #68b738;
  }
  .site-list__link {
    font-size: 18px;
    line-height: 24.86px;
    color: #222222;
  }
}
@media (min-width: 1440px) {
  .site-list__item {
    width: auto;
    margin-left: 36px;
    border-bottom: none;
  }
  .site-list__item--active {
    border-bottom: 2px solid;
    border-bottom-color: #ffffff;
  }
  .site-list__link {
    font-size: 20px;
    line-height: 24.86px;
    color: #ffffff;
  }
}

.page-main {
  background: url(/img/index-background-mobile.jpg) top center no-repeat;
  background-size: 100% auto;
}
@media (min-width: 768px) {
  .page-main {
    background: none;
  }
}

.programms {
  min-height: 1055px;
  min-width: 320px;
  margin-bottom: 25px;
}
.programms__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
.programms__header {
  width: 100%;
  min-height: 480px;
  text-align: center;
}
.programms__main-title {
  margin-bottom: 25px;
  padding-top: 25px;
  font-size: 36px;
  line-height: 36px;
  font-weight: inherit;
  color: #ffffff;
}
.programms__slogan {
  display: block;
  margin-bottom: 30px;
  font-size: 14px;
  font-weight: inherit;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
}
.programms__link {
  display: block;
  width: 100%;
  margin-bottom: 22px;
  font-size: 14px;
  font-weight: inherit;
  line-height: 18px;
  text-transform: uppercase;
}
.programms__slim, .programms__weight {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .programms {
    margin-bottom: 45px;
  }
  .programms__wrapper {
    padding: 0 30px;
  }
  .programms__flex-wrapper {
    width: 100%;
  }
  .programms__header {
    min-height: 850px;
    margin-left: 90px;
    text-align: left;
  }
  .programms__main-title {
    margin-bottom: 40px;
    font-size: 60px;
    line-height: 60px;
    color: #111111;
  }
  .programms__slogan {
    margin-bottom: 47px;
    font-size: 20px;
    line-height: 20px;
    color: #111111;
  }
  .programms__link {
    display: block;
    min-width: 245px;
    min-height: 52px;
    max-width: 280px;
    padding: 15px 0;
    font-size: 20px;
    line-height: 20px;
    text-align: center;
  }
}
@media (min-width: 1440px) {
  .programms {
    margin-bottom: 80px;
  }
  .programms__wrapper {
    padding: 0 110px;
  }
  .programms__flex-wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .programms__header {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 395px;
    margin-left: 80px;
    margin-bottom: 268px;
  }
  .programms__logo-image {
    position: absolute;
    top: 32px;
    left: 366px;
  }
}

