.advatanges {
  margin-bottom: 42px;
}
.advatanges__title {
  margin-bottom: 40px;
  font-size: 36px;
  font-weight: inherit;
  line-height: 60px;
}
.advatanges__wrapper {
  padding: 0 20px;
}
.advatanges__item {
  position: relative;
  min-width: 280px;
  min-height: 60px;
  padding-left: 80px;
  margin-bottom: 20px;
}
.advatanges__item::before, .advatanges__item:first-child::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  min-width: 60px;
  min-height: 60px;
  max-width: 80px;
  max-height: 80px;
  background-color: #68b738;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url(/img/advantage-1.png);
}
.advatanges__item:nth-child(2)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  min-width: 60px;
  min-height: 60px;
  max-width: 80px;
  max-height: 80px;
  background-color: #68b738;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url(/img/andvantage-2.png);
}
.advatanges__item:nth-child(3)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  min-width: 60px;
  min-height: 60px;
  max-width: 80px;
  max-height: 80px;
  background-color: #68b738;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url(/img/advantage-3.png);
}
.advatanges__item:nth-child(4)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  min-width: 60px;
  min-height: 60px;
  max-width: 80px;
  max-height: 80px;
  background-color: #68b738;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url(/img/advantage-4.png);
}
.advatanges__description {
  padding: 0;
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .advatanges {
    margin-bottom: 50px;
  }
  .advatanges__title {
    margin-top: 50px;
    margin-bottom: 75px;
    font-size: 60px;
  }
  .advatanges__wrapper {
    padding: 0 30px;
  }
  .advatanges__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .advatanges__item {
    width: 250px;
    min-width: auto;
    min-height: 230px;
    padding-top: 115px;
    padding-left: 0;
    margin-bottom: 89px;
  }
  .advatanges__item::after, .advatanges__item:first-child::after {
    display: block;
    position: absolute;
    top: -40px;
    right: 0;
    z-index: -1;
    width: 50%;
    height: 100%;
    padding: 0;
    margin: 0;
    color: #f2f2f2;
    font-size: 281.07px;
    line-height: 281.07px;
    content: "1";
  }
  .advatanges__item:nth-child(2)::after {
    display: block;
    position: absolute;
    top: -40px;
    right: 0;
    z-index: -1;
    width: 50%;
    height: 100%;
    padding: 0;
    margin: 0;
    color: #f2f2f2;
    font-size: 281.07px;
    line-height: 281.07px;
    content: "2";
  }
  .advatanges__item:nth-child(3)::after {
    display: block;
    position: absolute;
    top: -40px;
    right: 0;
    z-index: -1;
    width: 50%;
    height: 100%;
    padding: 0;
    margin: 0;
    color: #f2f2f2;
    font-size: 281.07px;
    line-height: 281.07px;
    content: "3";
  }
  .advatanges__item:nth-child(4)::after {
    display: block;
    position: absolute;
    top: -40px;
    right: 0;
    z-index: -1;
    width: 50%;
    height: 100%;
    padding: 0;
    margin: 0;
    color: #f2f2f2;
    font-size: 281.07px;
    line-height: 281.07px;
    content: "4";
  }
  .advatanges__description {
    font-size: 16px;
    line-height: 24px;
  }
}

.example__title {
  margin-bottom: 60px;
  margin-left: 110px;
  font-size: 60px;
  line-height: 60px;
  font-weight: inherit;
}
.example__wrapper {
  display: flex;
}
.example__description-wrapper {
  display: flex;
  flex-direction: column;
  padding-left: 110px;
  padding-right: 175px;
  max-width: 720px;
  background-color: #f2f2f2;
}
.example__description {
  margin-top: 75px;
  margin-bottom: 70px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #444444;
}
.example__result-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 60px;
}
.example__result, .example__result-legend, .example__result-info {
  transform: rotate(180deg);
  text-align: center;
}
.example__result {
  margin: 0;
  padding: 0;
  width: 162px;
  height: 80px;
  box-sizing: border-box;
  margin-right: 42px;
  margin-bottom: 10px;
  border: 1px solid;
  border-color: #cdcdcd;
  border-radius: 3px;
}
.example__result-legend {
  height: 30px;
}
.example__result-legend-text {
  margin: 0 8px;
  padding-bottom: 10px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 14px;
}
.example__result-info {
  display: inline-block;
  margin: 0;
  font-size: 30px;
  line-height: 37.02px;
  text-transform: uppercase;
}
.example__price {
  margin-bottom: 10px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: inherit;
}
.example__price-info {
  margin-left: 67px;
}
.example__slider-wrapper {
  display: flex;
  width: 50%;
  min-height: 460px;
  position: relative;
  background-color: #f2f2f2;
}
.example__before, .example__after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.example__before {
  background-color: #f2f2f2;
}
.example__after {
  display: none;
  background-color: #eaeaea;
}
.example__before-image, .example__after-image {
  position: absolute;
  top: -30%;
  left: 50%;
  z-index: 2;
  transform: translateX(-50%);
}
.example__slider {
  position: absolute;
  bottom: 45px;
  left: 50%;
  z-index: 3;
  margin: auto;
  transform: translateX(-50%);
}

.slider__before-toggle, .slider__after-toggle {
  border-bottom: 1px dotted;
  border-bottom-color: #bebebe;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

.contacts__wrapper {
  position: relative;
}
.contacts__info {
  position: absolute;
  top: 95px;
  left: 110px;
  z-index: 1;
}
.contacts__map {
  width: 100%;
  height: 400px;
}

.info {
  display: flex;
  justify-content: space-between;
  width: 570px;
  height: 201px;
  padding: 50px 70px 60px 75px;
  background-color: #ffffff;
}
.info__title {
  margin: 0;
  margin-right: 95px;
  font-size: 20px;
  line-height: 30px;
  font-weight: inherit;
  text-transform: uppercase;
}
.info__address {
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
}
.info__street {
  margin: 0;
  margin-bottom: 19px;
}
.info__city {
  margin: 0;
}

.page-footer {
  min-height: 150px;
  background-color: #f2f2f2;
}
.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  margin: auto 110px;
  min-height: 150px;
  height: 100%;
}
.page-footer__logo, .page-footer__social, .page-footer__copyright {
  margin: auto 0;
}

.social__list {
  display: flex;
  justify-content: space-between;
}
.social__item {
  margin-right: 28px;
  margin-bottom: 10px;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 28px;
}

.copyright {
  display: flex;
  justify-content: space-between;
}
.copyright__text {
  margin: auto;
  margin-right: 22px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-weight: inherit;
  color: #444444;
}